import { Container, createStyles, makeStyles, Theme } from '@material-ui/core'
import { useEffect } from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import Bg from '../assets/bg-session.png'
import Header from '../components/public/header'
import Login from './login'
import Register from './register'
import ResetPassword from './resetPassword'
import { Dashboard } from './app/dashboard'
import { ManageUsers } from './app/manage-users'
import { Client } from './app/client'
import { DrawerHeader } from '../components/private/DrawerHeader'

export function Pages() {
  useEffect(() => {
    document.title = 'Santafe'
  }, [])
  //const { isAuth } = useJWT()
  const role = localStorage.getItem('role')
  const classes = useStyles()

  //return <PrivateApp />

  return (
    <>
      <Switch>
        <Route path='/entrar' render={() => <div>
          <Header />
          <main className={classes.main}>
            <Container className={classes.container} maxWidth={'lg'}>
              <Login />
            </Container>
            <img className={classes.img} src={Bg} alt='backgroundImage' />
          </main>
        </div>} />
        <Route path='/cadastrar' render={() => <div>
          <Header />
          <main className={classes.main}>
            <Container className={classes.container} maxWidth={'lg'}>
              <Register />
            </Container>
            <img className={classes.img} src={Bg} alt='backgroundImage' />
          </main>
        </div>} />
        <Route path='/recuperar' render={() => <div>
          <Header />
          <main className={classes.main}>
            <Container className={classes.container} maxWidth={'lg'}>
              <ResetPassword />
            </Container>
            <img className={classes.img} src={Bg} alt='backgroundImage' />
          </main>
        </div>} />
        <Route path='/dashboard' render={() => <div><DrawerHeader /><Dashboard /></div>} />
        <Route path='/client' render={() => <div><DrawerHeader /><Client /></div>} />
        <Route path='/cadastros' render={() => <div><DrawerHeader /><ManageUsers /></div>} />
        <Route path='/' render={() => role ? (role === 'Cliente' ? <Redirect to='/dashboard' /> : <Redirect to='/cadastros' />) : <Redirect to='/entrar' />} />
      </Switch>
    </>
  )
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    main: {
      display: 'flex',
      flexDirection: 'row',
      overflow: 'hidden',
      maxHeight: '100vh',
    },
    container: {
      height: '100vh',
      paddingTop: 58,
      paddingBottom: 58,
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'center',
      alignItems: 'center',
      maxWidth: 288,
      overflow: 'hidden',
      [theme.breakpoints.up('sm')]: {
        maxWidth: 520,
      },
      [theme.breakpoints.up('lg')]: {
        maxWidth: '100vw',
        margin: 'initial',
        justifyContent: 'space-between',
        paddingLeft: 'initial',
        paddingRight: 'initial',
      },
      [theme.breakpoints.up('xl')]: {
        justifyContent: 'center',
      },
    },
    img: {
      position: 'relative',
      maxWidth: '50vw',
      height: '100vh',
      top: 0,
      transition: '0.5s',
      zIndex: theme.zIndex.appBar + 1,
      [theme.breakpoints.down('md')]: {
        display: 'none',
      },
    },
  })
)
