import {
  Box,
  Grid,
  Container,
  createStyles,
  makeStyles,
  Theme,
  LinearProgress,
  Modal,
  TextField,
  Typography,
  MenuItem,
  Snackbar,
  Button as MuiButton
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import React, { useState, useEffect } from 'react';
import { useHistory, useRouteMatch } from 'react-router'
import { useFormik } from 'formik'
import * as yup from 'yup'
import api from '../../../config/api'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 4,
};

interface User {
  id: number,
  nomeCliente: string,
  email: string,
  cnpjCliente: string,
  tecResponsavel: string,
  enderecoCliente: string,
  tipoDoLocal: string,
  role: string,
  telefone: string,
  codUsuario: string,
  dataCad: string
}

export function Client() {
  const classes = useStyles()
  const user = JSON.parse(localStorage.getItem('user'))
  const { container } = useStyles()
  // const [user, setUser] = useState(JSON.parse(localStorage.getItem('user')));
  const [isLoading, setIsLoading] = useState(false)
  const { push } = useHistory()
  const [message, setMessage] = useState('')
  const [msgType, setMsgType] = useState(0)


  const validationSchema = yup.object({
    currentPassword: yup.string(),
    newPassword: yup
      .string()
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
        'A senha deve conter 8 caracteres sendo 1 maisculo, 1 numrico e 1 caractere especial'
      ),
    confirmPassword: yup
      .string()
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
        'A senha deve conter 8 caracteres sendo 1 maisculo, 1 numrico e 1 caractere especial'
      )
      .test('match', 'as senhas no batem', function (confirmPassword) {
        return confirmPassword === this.parent.newPassword
      }),
  })


  const updatePassword = (values) => {
    setIsLoading(true)
    // delete values.confirmPassword
    api
      .post('/user/password', values)
      .then((result) => {
        if (result.status === 200) {
          setMsgType(1)
          // Password Changed
          setMessage('Senha Atualizada.')
        }
      })
      .catch((err) => {
        setMsgType(2)
        // Invaild Password
        setMessage("Senha invalida.")
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  const editSubmit = (values) => {
    setIsLoading(false)
    api
      .post('/user/edit', values)
      .then((result) => {
        if (result.status === 200) {
          localStorage.setItem("user", JSON.stringify(result.data))
          setMsgType(1)
          // Edit user success
          setMessage('Usurio actualizado com sucesso.')
        }
      })
      .catch((err) => {
        setMsgType(2)
        // setMessage("Invalid Info")
        setMessage("Informao invalida.")
      })
      .finally(() => {
        setIsLoading(false)
      })
  }

  const passwordForm = useFormik({
    initialValues: {
      codCliente: user.codCliente,
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
    },
    validationSchema,
    onSubmit: values => {
      updatePassword(values);
    },
  })

  const editForm = useFormik({
    initialValues: {
      cnpjCliente: user.cnpjCliente,
      email: user.email,
      enderecoCliente: user.enderecoCliente,
      id: user.id,
      nomeCliente: user.nomeCliente,
      role: user.role,
      tecResponsavel: user.tecResponsavel,
      telefone: user.telefone,
      tipoDoLocal: user.tipoDoLocal,
    },
    // initialValues: user,
    validationSchema,
    onSubmit: values => {
      editSubmit(values);
    },
  })

  return (
    <Container maxWidth='lg' className={container}>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={msgType > 0}
        autoHideDuration={3000}
        onClose={() => {
          setMsgType(0)
        }}
      >
        {
          msgType === 1 ? <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='success'>
            {message}
          </Alert> : (msgType === 2 && <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='error'>
            {message}
          </Alert>)
        }
      </Snackbar>
      {isLoading ? <LinearProgress classes={{ colorPrimary: classes.colorPrimary, barColorPrimary: classes.barColorPrimary }} /> : <></>}
      <Grid container spacing={2}>
        <Grid item xs={12} md={6} sm={12}>
          <Box
            className="col-md-6"
            style={{
              backgroundColor: '#FFFFFF',
              padding: '20px',
            }}>
            <form method='post' onSubmit={editForm.handleSubmit}>
              <Typography style={{ textAlign: 'center' }}><h2>Editar Usurio</h2></Typography>
              <TextField
                fullWidth
                id='nomeCliente'
                name='nomeCliente'
                label='Nome Cliente'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.nomeCliente}
                onChange={editForm.handleChange}
                error={editForm.touched.nomeCliente && Boolean(editForm.errors.nomeCliente)}
                helperText={editForm.touched.nomeCliente && editForm.errors.nomeCliente}
              />
              <TextField
                fullWidth
                id='email'
                name='email'
                label='Email'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.email}
                onChange={editForm.handleChange}
                error={editForm.touched.email && Boolean(editForm.errors.email)}
                helperText={editForm.touched.email && editForm.errors.email}
              />
              <TextField
                fullWidth
                id='telefone'
                name='telefone'
                label='Telefone'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.telefone}
                onChange={editForm.handleChange}
                error={editForm.touched.telefone && Boolean(editForm.errors.telefone)}
                helperText={editForm.touched.telefone && editForm.errors.telefone}
              />
              <TextField
                fullWidth
                id='tecResponsavel'
                name='tecResponsavel'
                label='Tcnico Responsvel'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.tecResponsavel}
                onChange={editForm.handleChange}
                error={editForm.touched.tecResponsavel && Boolean(editForm.errors.tecResponsavel)}
                helperText={editForm.touched.tecResponsavel && editForm.errors.tecResponsavel}
              />
              <TextField
                fullWidth
                id='enderecoCliente'
                name='enderecoCliente'
                label='Endereo'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.enderecoCliente}
                onChange={editForm.handleChange}
                error={editForm.touched.enderecoCliente && Boolean(editForm.errors.enderecoCliente)}
                helperText={editForm.touched.enderecoCliente && editForm.errors.enderecoCliente}
              />
              <TextField
                fullWidth
                id='cnpjCliente'
                name='cnpjCliente'
                label='CNPJ Cliente'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.cnpjCliente}
                onChange={editForm.handleChange}
                error={editForm.touched.cnpjCliente && Boolean(editForm.errors.cnpjCliente)}
                helperText={editForm.touched.cnpjCliente && editForm.errors.cnpjCliente}
              />
              <TextField id='role'
                name='role'
                label='Perfil' variant='outlined' color='secondary'
                className={classes.input}
                value={editForm.values.role}
                disabled
                onChange={editForm.handleChange} fullWidth select>
                <MenuItem value="Cliente">Cliente</MenuItem>
                <MenuItem value="Supervisor">Supervisor</MenuItem>
                <MenuItem value="Admin">Admin</MenuItem>
              </TextField>
              <TextField
                fullWidth
                id='tipoDoLocal'
                name='tipoDoLocal'
                label='Tipo do Local'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={editForm.values.tipoDoLocal}
                onChange={editForm.handleChange}
                error={editForm.touched.tipoDoLocal && Boolean(editForm.errors.tipoDoLocal)}
                helperText={editForm.touched.tipoDoLocal && editForm.errors.tipoDoLocal}
              />
              <MuiButton type="submit" className="mb-5" variant="contained" color="primary" style={{ backgroundColor: '#1f497d', width: '100%', marginBottom: '30px' }}>
                Atualizar
              </MuiButton>
            </form>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} sm={12}>
          <Box
            className="col-md-6"
            style={{
              backgroundColor: '#FFFFFF',
              padding: '20px',
              height: '400px'
            }}>
            <form method='post' onSubmit={passwordForm.handleSubmit}>
              <Typography style={{ textAlign: 'center' }}><h2>Atualizar Senha</h2></Typography>
              <TextField
                fullWidth
                id='currentPassword'
                name='currentPassword'
                label='Senha Atual'
                type='password'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={passwordForm.values.currentPassword}
                onChange={passwordForm.handleChange}
                error={passwordForm.touched.currentPassword && Boolean(passwordForm.errors.currentPassword)}
                helperText={passwordForm.touched.currentPassword && passwordForm.errors.currentPassword}
              />
              <TextField
                fullWidth
                id='newPassword'
                name='newPassword'
                label='Nova Senha'
                type='password'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={passwordForm.values.newPassword}
                onChange={passwordForm.handleChange}
                error={passwordForm.touched.newPassword && Boolean(passwordForm.errors.newPassword)}
                helperText={passwordForm.touched.newPassword && passwordForm.errors.newPassword}
              />
              <TextField
                fullWidth
                id='confirmPassword'
                name='confirmPassword'
                label='Digite a nova senha novamente'
                type='password'
                variant='outlined'
                color='secondary'
                className={classes.input}
                value={passwordForm.values.confirmPassword}
                onChange={passwordForm.handleChange}
                error={
                  passwordForm.touched.confirmPassword &&
                  Boolean(passwordForm.errors.confirmPassword)
                }
                helperText={
                  passwordForm.touched.confirmPassword && passwordForm.errors.confirmPassword
                }
              />
              <MuiButton type="submit" className="mb-5" variant="contained" color="primary" style={{ backgroundColor: '#1f497d', width: '100%', marginBottom: '30px' }}>
                Atualizar
              </MuiButton>
            </form>
          </Box>
        </Grid>
      </Grid>
    </Container>
  )
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      paddingTop: theme.spacing(2),
    },
    root: {
      display: 'flex',
      width: '100%',
    },
    form: {
      display: 'flex',
      flexDirection: 'column',
      flexGrow: 1,
    },
    input: {
      marginBottom: theme.spacing(2),
    },
    forgotLink: {
      textAlign: 'right',
    },
    submitBtn: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    divider: {
      display: 'flex',
      textAlign: 'center',
      '&>span': {
        marginBottom: 3.5,
        color: '#aaa',
      },
      '&::after': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginBottom: 3,
        marginLeft: theme.spacing(2),
      },
      '&::before': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginRight: theme.spacing(2),
        marginBottom: 3,
      },
    },
    icon: {
      color: '#fff',
      marginRight: theme.spacing(1.5),
    },
    paddingLeft: {
      paddingLeft: theme.spacing(1),
    },
    paddingRight: {
      paddingRight: theme.spacing(1),
    },
    facebookButton: {
      background: '#1479BD',
    },
    googleButton: {
      background: '#DD4B38',
    },
    modal: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      display: 'flex',
      justifyContent: 'center',
      alignContent: 'center',
      width: 412,
      height: 315,
      backgroundColor: '#fafafa',
      transform: 'translate(-50%, -50%)',
    },
    containerModal: {},
    colorPrimary: {
      backgroundColor: '#1f497d',
    },
    barColorPrimary: {
      backgroundColor: '#ffffff',
    },
  })
)