import { makeStyles, Theme, Typography } from '@material-ui/core'
import { createStyles } from '@material-ui/styles'
import { memo } from 'react'
import AvatarImageMock from '../../../../../assets/avatar.png'

export interface UserProps {
  avatar?: string
  name?: string
  isUserAdmin?: boolean
}

const User = ({
  avatar,
  name = 'Rogrio Dantas',
  isUserAdmin = true,
}: UserProps) => {
  const classes = useStyles()
  const imageSource = avatar || AvatarImageMock
  const myname = localStorage.getItem('name')
  const role = localStorage.getItem('role')
  return (
    <div className={classes.container} style={{ backgroundColor: '#1f497d' }}>
      <img src={imageSource} alt={name} style={{ height: '85px' }} />
      <Typography variant='h6' align='center'>
        {myname}
        {isUserAdmin && (
          <span>
            <br /> ({role})
          </span>
        )}
      </Typography>
    </div>
  )
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      display: 'flex',
      height: '15rem',
      flexDirection: 'column',
      width: '280px',
      gap: '1rem',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: theme.palette.primary.main,
      color: '#fff',
    },
  })
)

export const MemoizedUser = memo(User)
