import {
  Box,
  Container,
  createStyles,
  makeStyles,
  Theme,
  Grid,
  LinearProgress,
  Modal,
  TextField,
  Typography,
  MenuItem,
  Snackbar,
  Button as MuiButton
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router'
import { useFormik } from 'formik'
import * as yup from 'yup'
import ClientsTable from '../../../components/private/ClientsTable'
import { Input } from '../../../components/private/Input'
import { Button, ItemsList } from './styles'
import MoreHorizIcon from '@material-ui/icons/MoreHoriz'
import api from '../../../config/api'
import './style.css'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 4,
};

interface User {
  id: number,
  nomeCliente: string,
  email: string,
  cnpjCliente: string,
  tecResponsavel: string,
  enderecoCliente: string,
  tipoDoLocal: string,
  role: string,
  telefone: string,
  codUsuario: string,
  dataCad: string
}

export function ManageUsers() {
  const classes = useStyles()
  const role = localStorage.getItem('role')
  const { container } = useStyles()
  const [inputValue, setInputValue] = useState('')
  const [isActionsOpened, setIsActionsOpened] = useState(false)
  const [openCreate, setCreateModalOpen] = useState(false)
  const [editOpen, setEditModalOpen] = useState(false)
  const [users, setUsers] = useState([])
  const [selectedRows, setSelectedRows] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const { push } = useHistory()
  const [message, setMessage] = useState('')
  const [msgType, setMsgType] = useState(0)

  const handleToggleActionsOpened = (state: boolean) => {
    setIsActionsOpened(state)
  }

  const handleCreate = () => {
    setCreateModalOpen(true)
  }

  const handleEdit = () => {
    if (selectedRows.length == 1) {
      const editUser = users.find(user => user.id == selectedRows[0])
      editForm.setValues(editUser)
      setEditModalOpen(true)
    } else {
      setMsgType(2)
      // setMessage('Select only one user')
      setMessage('Escolha um Usurio apenas.')
    }
  }

  const handleViewReport = () => {
    if (selectedRows.length == 1) {
      const reportUser = users.find(user => user.id == selectedRows[0])
      localStorage.setItem('code', reportUser.id)
      push('/dashboard')
    } else {
      setMsgType(2)
      // setMessage('Select only one user')
      setMessage('Escolha um Usurio apenas.')
    }
  }

  const handleDelete = () => {
    if (selectedRows.length >= 1) {
      if (confirm("Do you want to delete these values?")) {
        api
          .post('/user/delete', selectedRows)
          .then((result) => {
            if (result.status === 200) {
              setMsgType(1)
              // setMessage('Delete user success')
              setMessage('Usurio apagado com sucesso.')
              getUserData()
            }
          })
          .catch((err) => {
            console.log(err)
            setMsgType(2)
            setMessage(err)
          })
          .finally(() => {
            setEditModalOpen(false)
            setIsLoading(false)
          })
      }
    } else {
      setMsgType(2)
      // setMessage('Please select user')
      setMessage('Por favor escolha um Usurio')
    }
  }

  const validationSchema = yup.object({
    nomeCliente: yup.string().required('O Primeiro nome  obrigatrio'),
    email: yup
      .string()
      .email('insira um E-mail vlido')
      .required('E-mail  obrigatrio'),
    password: yup
      .string()
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
        'A senha deve conter 8 caracteres sendo 1 maisculo, 1 numrico e 1 caractere especial'
      ),
    confirmPassword: yup
      .string()
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
        'A senha deve conter 8 caracteres sendo 1 maisculo, 1 numrico e 1 caractere especial'
      )
      .test('match', 'as senhas no batem', function (confirmPassword) {
        return confirmPassword === this.parent.password
      }),
  })

  const editValidationSchema = yup.object({
    nomeCliente: yup.string().required('O Primeiro nome  obrigatrio'),
    email: yup
      .string()
      .email('insira um E-mail vlido')
      .required('E-mail  obrigatrio'),
  })

  const createSubmit = (values) => {
    delete values.confirmPassword
    api
      .post('/user/create', values)
      .then((result) => {
        if (result.status === 200) {
          getUserData()
          setMsgType(1)
          // setMessage('Create user success')
          setMessage('Usurio criado com sucesso')
        }
      })
      .catch((err) => {
        setMsgType(2)
        setMessage(err)
        console.log(err)
      })
      .finally(() => {
        setCreateModalOpen(false)
        setIsLoading(false)
      })
  }

  const editSubmit = (values) => {
    api
      .post('/user/edit', values)
      .then((result) => {
        if (result.status === 200) {
          getUserData()
          setMsgType(1)
          // setMessage('Edit user success')
          setMessage('Usurio editado com sucesso')
        }
      })
      .catch((err) => {
        setMsgType(1)
        setMessage(err)
        console.log(err)
      })
      .finally(() => {
        setEditModalOpen(false)
        setIsLoading(false)
      })
  }

  const createForm = useFormik({
    initialValues: {
      nomeCliente: '',
      email: '',
      telefone: '',
      tecResponsavel: '',
      enderecoCliente: '',
      cnpjCliente: '',
      role: 'Cliente',
      tipoDoLocal: '',
      password: '',
      confirmPassword: '',
    },
    validationSchema,
    onSubmit: values => {
      createSubmit(values);
    },
  })

  const editForm = useFormik({
    initialValues: {
      cnpjCliente: "",
      email: "",
      enderecoCliente: "",
      id: 1004,
      nomeCliente: "",
      role: "",
      tecResponsavel: "",
      telefone: "",
      tipoDoLocal: "",
    },
    validationSchema,
    onSubmit: values => {
      editSubmit(values);
    },
  })

  const getUserData = () => {
    api
      .post('/user/all')
      .then((result) => {
        if (result.status === 200) {
          const rusers: any[] = [];
          result.data.map((user: any) => rusers.push({ id: user.codCliente, nomeCliente: user.nomeCliente, cnpjCliente: user.cnpjCliente, tecResponsavel: user.tecResponsavel, enderecoCliente: user.enderecoCliente, tipoDoLocal: user.tipoDoLocal, email: user.email, role: user.role, telefone: user.telefone, codUsuario: user.codUsuario, dataCad: user.dataCad }))
          setUsers(rusers)
        }
      })
      .catch((err) => console.log(err))
      .finally(() => console.log('finally'))
  }

  const handleChangeSelection = model => {
    setSelectedRows(model)
  }

  useEffect(() => {
    getUserData()
    return () => {
      console.log("Will Un Mount")
    }
  }, []);
  return (
    <Container maxWidth='lg' className={container}>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={msgType > 0}
        autoHideDuration={3000}
        onClose={() => {
          setMsgType(0)
        }}
      >
        {
          msgType === 1 ? <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='success'>
            {message}
          </Alert> : (msgType === 2 && <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='error'>
            {message}
          </Alert>)
        }
      </Snackbar>
      {isLoading ? <LinearProgress classes={{ colorPrimary: classes.colorPrimary, barColorPrimary: classes.barColorPrimary }} /> : <></>}
      <Grid container spacing={2}>
        <Grid item xs={12} md={6} sm={6} >
          <Input
            inputValue={inputValue}
            setInputValue={setInputValue}
            placeholder='Digite um nome...'
            legendText='Procurar'
          />
        </Grid>
        <Grid item xs={12} md={6} sm={6} >
          <Box display='flex' alignItems='center' justifyContent='flex-end' style={{ marginTop: '10px' }}>
            {role === 'Admin' && <Button onClick={e => handleCreate()} style={{ backgroundColor: '#1f497d', marginLeft: '10px' }}>Criar</Button>}
            {role === 'Admin' && <Button onClick={e => handleEdit()} style={{ backgroundColor: '#1f497d', marginLeft: '10px' }}>Editar</Button>}
            {role === 'Admin' && <Button onClick={e => handleDelete()} style={{ backgroundColor: '#1f497d', marginLeft: '10px' }}>Apagar</Button>}
            <Button onClick={e => handleViewReport()} style={{ backgroundColor: '#1f497d', marginLeft: '10px' }}>Relatorio</Button>
          </Box>
        </Grid>
      </Grid>

      <Modal
        open={openCreate}
        className={classes.modal}
        onClose={() => setCreateModalOpen(false)}
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
      >
        <Box
          style={{
            width: 500,
            backgroundColor: '#FFFFFF',
            padding: '20px',
            overflowY: 'auto'
          }}>
          <form method='post' onSubmit={createForm.handleSubmit} style={{ height: '450px' }}>
            <Typography style={{ textAlign: 'center' }}><h2>Criar novo Usurio</h2></Typography>
            <TextField
              fullWidth
              id='nomeCliente'
              name='nomeCliente'
              label='Nome Cliente'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.nomeCliente}
              onChange={createForm.handleChange}
              error={createForm.touched.nomeCliente && Boolean(createForm.errors.nomeCliente)}
              helperText={createForm.touched.nomeCliente && createForm.errors.nomeCliente}
            />
            <TextField
              fullWidth
              id='email'
              name='email'
              label='Email'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.email}
              onChange={createForm.handleChange}
              error={createForm.touched.email && Boolean(createForm.errors.email)}
              helperText={createForm.touched.email && createForm.errors.email}
            />
            <TextField
              fullWidth
              id='telefone'
              name='telefone'
              label='Telefone'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.telefone}
              onChange={createForm.handleChange}
              error={createForm.touched.telefone && Boolean(createForm.errors.telefone)}
              helperText={createForm.touched.telefone && createForm.errors.telefone}
            />
            <TextField
              fullWidth
              id='tecResponsavel'
              name='tecResponsavel'
              label='Tcnico Responsvel'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.tecResponsavel}
              onChange={createForm.handleChange}
              error={createForm.touched.tecResponsavel && Boolean(createForm.errors.tecResponsavel)}
              helperText={createForm.touched.tecResponsavel && createForm.errors.tecResponsavel}
            />
            <TextField
              fullWidth
              id='enderecoCliente'
              name='enderecoCliente'
              label='Endereo'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.enderecoCliente}
              onChange={createForm.handleChange}
              error={createForm.touched.enderecoCliente && Boolean(createForm.errors.enderecoCliente)}
              helperText={createForm.touched.enderecoCliente && createForm.errors.enderecoCliente}
            />
            <TextField
              fullWidth
              id='cnpjCliente'
              name='cnpjCliente'
              label='CNPJ Cliente'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.cnpjCliente}
              onChange={createForm.handleChange}
              error={createForm.touched.cnpjCliente && Boolean(createForm.errors.cnpjCliente)}
              helperText={createForm.touched.cnpjCliente && createForm.errors.cnpjCliente}
            />
            <TextField
              id='role'
              name='role'
              label='Perfil'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.role}
              onChange={createForm.handleChange} fullWidth select>
              <MenuItem value="Cliente">Cliente</MenuItem>
              <MenuItem value="Supervisor">Supervisor</MenuItem>
              <MenuItem value="Admin">Admin</MenuItem>
            </TextField>
            <TextField
              fullWidth
              id='tipoDoLocal'
              name='tipoDoLocal'
              label='Tipo do Local'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.tipoDoLocal}
              onChange={createForm.handleChange}
              error={createForm.touched.tipoDoLocal && Boolean(createForm.errors.tipoDoLocal)}
              helperText={createForm.touched.tipoDoLocal && createForm.errors.tipoDoLocal}
            />

            <TextField
              fullWidth
              id='password'
              name='password'
              label='Senha'
              type='password'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.password}
              onChange={createForm.handleChange}
              error={createForm.touched.password && Boolean(createForm.errors.password)}
              helperText={createForm.touched.password && createForm.errors.password}
            />
            <TextField
              fullWidth
              id='confirmPassword'
              name='confirmPassword'
              label='Confirmar Senha'
              type='password'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={createForm.values.confirmPassword}
              onChange={createForm.handleChange}
              error={
                createForm.touched.confirmPassword &&
                Boolean(createForm.errors.confirmPassword)
              }
              helperText={
                createForm.touched.confirmPassword && createForm.errors.confirmPassword
              }
            />
            <MuiButton type="submit" className="mb-5" variant="contained" color="primary" style={{ width: '100%', marginBottom: '30px', backgroundColor: '#1f497d' }}>
              Criar
            </MuiButton>
          </form>
        </Box>
      </Modal>

      <Modal
        open={editOpen}
        className={classes.modal}
        onClose={() => setEditModalOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          style={{
            width: 500,
            backgroundColor: '#FFFFFF',
            padding: '20px',
            overflowY: 'auto'
          }}>
          <form onSubmit={editForm.handleSubmit} style={{ height: '450px' }}>
            <Typography style={{ textAlign: 'center' }}><h2>Editar Usurio</h2></Typography>
            <TextField
              fullWidth
              id='nomeCliente'
              name='nomeCliente'
              label='Nome Cliente'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.nomeCliente}
              onChange={editForm.handleChange}
              error={editForm.touched.nomeCliente && Boolean(editForm.errors.nomeCliente)}
              helperText={editForm.touched.nomeCliente && editForm.errors.nomeCliente}
            />
            <TextField
              fullWidth
              id='email'
              name='email'
              label='Email'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.email}
              onChange={editForm.handleChange}
              error={editForm.touched.email && Boolean(editForm.errors.email)}
              helperText={editForm.touched.email && editForm.errors.email}
            />
            <TextField
              fullWidth
              id='telefone'
              name='telefone'
              label='Telefone'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.telefone}
              onChange={editForm.handleChange}
              error={editForm.touched.telefone && Boolean(editForm.errors.telefone)}
              helperText={editForm.touched.telefone && editForm.errors.telefone}
            />
            <TextField
              fullWidth
              id='tecResponsavel'
              name='tecResponsavel'
              label='Tcnico Responsvel'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.tecResponsavel}
              onChange={editForm.handleChange}
              error={editForm.touched.tecResponsavel && Boolean(editForm.errors.tecResponsavel)}
              helperText={editForm.touched.tecResponsavel && editForm.errors.tecResponsavel}
            />
            <TextField
              fullWidth
              id='enderecoCliente'
              name='enderecoCliente'
              label='Endereo'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.enderecoCliente}
              onChange={editForm.handleChange}
              error={editForm.touched.enderecoCliente && Boolean(editForm.errors.enderecoCliente)}
              helperText={editForm.touched.enderecoCliente && editForm.errors.enderecoCliente}
            />
            <TextField
              fullWidth
              id='cnpjCliente'
              name='cnpjCliente'
              label='CNPJ Cliente'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.cnpjCliente}
              onChange={editForm.handleChange}
              error={editForm.touched.cnpjCliente && Boolean(editForm.errors.cnpjCliente)}
              helperText={editForm.touched.cnpjCliente && editForm.errors.cnpjCliente}
            />
            <TextField id='role'
              name='role'
              label='Perfil' variant='outlined' color='secondary'
              className={classes.input}
              value={editForm.values.role}
              onChange={editForm.handleChange} fullWidth select>
              <MenuItem value="Cliente">Cliente</MenuItem>
              <MenuItem value="Supervisor">Supervisor</MenuItem>
              <MenuItem value="Admin">Admin</MenuItem>
            </TextField>
            <TextField
              fullWidth
              id='tipoDoLocal'
              name='tipoDoLocal'
              label='Tipo do Local'
              variant='outlined'
              color='secondary'
              className={classes.input}
              value={editForm.values.tipoDoLocal}
              onChange={editForm.handleChange}
              error={editForm.touched.tipoDoLocal && Boolean(editForm.errors.tipoDoLocal)}
              helperText={editForm.touched.tipoDoLocal && editForm.errors.tipoDoLocal}
            />

            <MuiButton type="submit" className="mb-5" variant="contained" color="primary" style={{ width: '100%', marginBottom: '30px', backgroundColor: '#1f497d' }}>
              Editar
            </MuiButton>
          </form>
        </Box>
      </Modal>
      <ClientsTable searchTerm={inputValue} users={users} onSelectionChange={handleChangeSelection} />
    </Container>
  )
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      paddingTop: theme.spacing(2),
    },
    colorPrimary: {
      backgroundColor: '#1f497d',
    },
    barColorPrimary: {
      backgroundColor: '#ffffff',
    },
    root: {
      display: 'flex',
      width: '100%',
    },
    form: {
      display: 'flex',
      flexDirection: 'column',
      flexGrow: 1,
    },
    input: {
      marginBottom: theme.spacing(2),
    },
    forgotLink: {
      textAlign: 'right',
    },
    submitBtn: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    divider: {
      display: 'flex',
      textAlign: 'center',
      '&>span': {
        marginBottom: 3.5,
        color: '#aaa',
      },
      '&::after': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginBottom: 3,
        marginLeft: theme.spacing(2),
      },
      '&::before': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginRight: theme.spacing(2),
        marginBottom: 3,
      },
    },
    icon: {
      color: '#fff',
      marginRight: theme.spacing(1.5),
    },
    paddingLeft: {
      paddingLeft: theme.spacing(1),
    },
    paddingRight: {
      paddingRight: theme.spacing(1),
    },
    facebookButton: {
      background: '#1479BD',
    },
    googleButton: {
      background: '#DD4B38',
    },
    containerModal: {},
    modal: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    paper: {
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  })
)