import { Container, createStyles, LinearProgress, makeStyles, Snackbar, Theme } from '@material-ui/core'

import { Alert, AlertTitle } from '@material-ui/lab'

import Grid from '@material-ui/core/Grid';

import { Chart } from '../../../components/private/Chart'
// import { Drawer } from '../../../components/private/Drawer'
import { ChartsContainer } from './styles'

import 'date-fns';
import React, { useState, useEffect } from 'react';
import { Redirect } from 'react-router'
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
  DatePicker
} from '@material-ui/pickers';

import api from '../../../config/api'

export function Dashboard() {

  const classes = useStyles()
  const code = localStorage.getItem('code')

  const [message, setMessage] = useState('')
  const [msgType, setMsgType] = useState(0)

  const [loading, setLoading] = useState(false)

  const [startDate, setStartSelectedDate] = useState(new Date(new Date().getFullYear(), 0, 1));
  const [endDate, setEndSelectedDate] = useState(new Date(new Date().getFullYear(), 11, 31));

  const handleStartDateChange = (date: any) => {
    if (date.getTime() <= endDate.getTime()) {
      setStartSelectedDate(date);
      getReportData(date, endDate)
    } else {
      setMsgType(2)
      // setMessage('Please selece correct date!')
      setMessage('Escolha a data correcta !')
    }
  };

  const handleEndDateChange = (date: any) => {
    if (startDate.getTime() <= date.getTime()) {
      setEndSelectedDate(date);
      getReportData(startDate, date)
    } else {
      setMsgType(2)
      // setMessage('Please selece correct date!')
      setMessage('Escolha a data correcta !')
    }
  };

  const [lineChartData, setLineChartData] = useState([
    ['Date', 'Quantidade']
  ]);

  const [barChartData1, setBarChartData1] = useState([
    ['', 'Normas Nao atendidas']
  ]);

  const [barChartData3, setBarChartData3] = useState([
    ['', 'No Conforme']
  ]);

  const [barChartData2, setBarChartData2] = useState([
    ['', 'Gravidade Baixa', 'Gravidade Media', 'Gravidade Alta']
  ]);

  const [pieChartData1, setPieChartData1] = useState([
    ['Item Name', 'Conforme']
  ]);

  const [pieChartData2, setPieChartData2] = useState([
    ['Item Name', 'Naoconforme']
  ]);

  const formatDate = (date: Date) => {
    const d = new Date(date)
    let month = '' + (d.getMonth() + 1), day = '' + d.getDate()
    const year = d.getFullYear()

    if (month.length < 2)
      month = '0' + month;
    if (day.length < 2)
      day = '0' + day;

    return [year, month, day].join('-');
  }

  const getReportData = (start, end) => {
    setLoading(true)
    const code = localStorage.getItem('code')
    api
      .get(`/qualification?codCliente=${code}&start=${formatDate(start)}&end=${formatDate(end)}`)
      .then((result) => {
        if (result.status === 200) {
          const pConformData = [['Item Name', 'Conforme']]
          const pNonConformData = [['Item Name', 'Naoconforme']]
          const bChartData2 = [['', 'Gravidade Baixa', 'Gravidade Media', 'Gravidade Alta']]
          result.data.map((dat: { itemName: string; conforme: string; }) => pConformData.push([dat.itemName, dat.conforme]))
          result.data.map((dat: { itemName: string; naoConforme: string; }) => pNonConformData.push([dat.itemName, dat.naoConforme]))
          result.data.filter(dat => dat.naoConforme !== 0).map((dat: { itemName: string; gravidade1: string; gravidade2: string, gravidade3: string }) => bChartData2.push([`${dat.itemName}`, dat.gravidade1, dat.gravidade2, dat.gravidade3]))
          setPieChartData1(pNonConformData)
          setPieChartData2(pConformData)
          setBarChartData2(bChartData2)
        }
      })
      .catch((err) => console.log(err))
      .finally(() => setLoading(false))

    api
      .get(`/standard?codCliente=${code}&start=${formatDate(start)}&end=${formatDate(end)}`)
      .then((result) => {
        if (result.status === 200) {
          const bChartData1 = [['', 'Normas Nao atendidas']]
          result.data.map((dat: { norma: string; total: string; }) => bChartData1.push([dat.norma, dat.total]))
          setBarChartData1(bChartData1)
        }
      })
      .catch((err) => console.log(err))
      .finally(() => setLoading(false))

    api
      .get(`/local?codCliente=${code}&start=${formatDate(start)}&end=${formatDate(end)}`)
      .then((result) => {
        if (result.status === 200) {
          const bChartData3 = [['', 'No Conforme']]
          result.data.map((dat: { nomeBloco: string; total: string; }) => bChartData3.push([dat.nomeBloco, dat.total]))
          setBarChartData3(bChartData3)
        }
      })
      .catch((err) => console.log(err))
      .finally(() => setLoading(false))

    api
      .get(`/nonconformityamount?codCliente=${code}&start=${formatDate(start)}&end=${formatDate(end)}`)
      .then((result) => {
        if (result.status === 200) {
          const lChartData = [['Date', 'Quantidade']]
          result.data.map((dat: { checkDate: string; amount: string; }) => lChartData.push([dat.checkDate, dat.amount]))
          setLineChartData(lChartData)
        }
      })
      .catch((err) => console.log(err))
      .finally(() => setLoading(false))

  }

  useEffect(() => {
    getReportData(startDate, endDate)
    return () => {
      console.log("Will Un Mount")
    }
  }, []);

  if (!code) {
    return <Redirect to='/cadastros' />
  }

  return (
    <>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={msgType > 0}
        autoHideDuration={3000}
        onClose={() => {
          setMsgType(0)
        }}
      >
        {
          msgType === 1 ? <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='success'>
            {message}
          </Alert> : (msgType === 2 && <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='error'>
            {message}
          </Alert>)
        }
      </Snackbar>
      {loading ? <LinearProgress classes={{ colorPrimary: classes.colorPrimary, barColorPrimary: classes.barColorPrimary }} /> : <></>}
      <div className="m-5">
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Grid container style={{ justifyContent: "center" }}>
            <KeyboardDatePicker
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-inline"
              label="Data de Incio"
              value={startDate}
              onChange={handleStartDateChange}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
              style={{ margin: '20px' }}
            />
            <KeyboardDatePicker
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-inline"
              label="Data do Fim"
              value={endDate}
              onChange={handleEndDateChange}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
              style={{ margin: '20px' }}
            />
          </Grid>
        </MuiPickersUtilsProvider>
      </div>
      <Container maxWidth='lg' className={classes.container} style={{ marginBottom: '50px' }}>
        <ChartsContainer>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <Chart
                title='Conformidade'
                chartType='PieChart'
                data={pieChartData2}
                options={{
                  'is3D': true,
                  'legend': 'bottom',
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <Chart
                title='No Conformidade'
                chartType='PieChart'
                data={pieChartData1}
                options={{
                  is3D: true,
                  'legend': 'bottom',
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <Chart
                title='Gravidade'
                chartType='BarChart'
                data={barChartData2}
                options={
                  {
                    colors: ['rgb(31, 73, 125)', 'orange', 'red'],
                    legend: { position: 'bottom', maxLines: 3 },
                    hAxis: {
                      title: '',
                    },
                    vAxis: {
                      title: '',
                    },
                  }
                }
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <Chart
                title='Quantidade de No Conformidade'
                chartType='LineChart'
                data={lineChartData}
                options={{
                  'legend': 'bottom',
                  hAxis: {
                    title: '',
                  },
                  vAxis: {
                    title: '',
                  },
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <Chart
                title='Normas'
                chartType='BarChart'
                data={barChartData1}
                options={
                  {
                    colors: ['rgb(31, 73, 125)', 'orange', 'red'],
                    'legend': 'bottom',
                  }
                }
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <Chart
                title='Relatrio No Conformidade Por Locais'
                chartType='BarChart'
                data={barChartData3}
                options={
                  {
                    colors: ['rgb(31, 73, 125)', 'orange', 'red'],
                    'legend': 'bottom',
                  }
                }
              />
            </Grid>
          </Grid>
        </ChartsContainer>
        {/* <Drawer /> */}
      </Container>
    </>
  )
}



const useStyles = makeStyles((theme) =>
  createStyles({
    container: {
      paddingTop: theme.spacing(2),
    },
    colorPrimary: {
      backgroundColor: '#1f497d',
    },
    barColorPrimary: {
      backgroundColor: '#ffffff',
    }
  })
)
