import {
  Button,
  createStyles,
  Grid,
  LinearProgress,
  Link,
  TextField,
  Theme,
  Typography,
  Snackbar
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import { makeStyles } from '@material-ui/styles'
import { useFormik } from 'formik'
import PropTypes, { InferProps } from 'prop-types'
import { Redirect } from 'react-router'
import { useHistory } from 'react-router'
import { useState } from 'react'
import * as yup from 'yup'
import api from '../../config/api'
import { useJWT } from '../../hooks/useJWT'

const validationSchema = yup.object({
  email: yup
    .string()
    .email('Insira um E-mail vlido')
    .required('E-mail  obrigatrio'),
  password: yup
    .string()
    .min(8, 'A senha deve conter no mnimo 8 caractres')
    .required('Senha  obrigatria'),
})

function FormLogin() {
  const classes = useStyles()
  const appId = process.env.REACT_APP_FACEBOOK_APP_ID || '4300458399993707'
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')
  const [msgType, setMsgType] = useState(0)
  // const [location, setLocation] = useState('')
  const { push } = useHistory()

  // const handleFacebookLogin = (user: any, accessToken: string) => {
  //   console.log(user)
  //   console.log(accessToken)
  // }

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    validationSchema,
    onSubmit: (values) => {
      setLoading(true)
      api
        .post('/user/login', values)
        .then((result) => {
          if (result.status === 200) {
            const { role, nomeCliente, codCliente } = result.data
            localStorage.setItem('role', role)
            localStorage.setItem('name', nomeCliente)
            localStorage.setItem('user', JSON.stringify(result.data))
            if (role === 'Cliente') {
              localStorage.setItem('code', codCliente)
            }
            push(role === 'Cliente' ? '/dashboard' : '/cadastros')
          }
        })
        .catch((err) => {
          setMsgType(2)
          // setMessage('Invalid Email or Password')
          setMessage('Senha Ou Email est incorrecto')
          console.log(err)
        })
        .finally(() => {
          setLoading(false)
        })
    },
  })

  return (
    <Grid item className={classes.root}>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={msgType > 0}
        autoHideDuration={3000}
        onClose={() => {
          setMsgType(0)
        }}
      >
        {
          msgType === 1 ? <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='success'>
            {message}
          </Alert> : (msgType === 2 && <Alert onClose={() => {
            setMsgType(0)
          }} variant='filled' severity='error'>
            {message}
          </Alert>)
        }
      </Snackbar>
      <form className={classes.form} onSubmit={formik.handleSubmit}>
        {loading ? <LinearProgress classes={{ colorPrimary: classes.colorPrimary, barColorPrimary: classes.barColorPrimary }} /> : <></>}
        <TextField
          fullWidth
          id='email'
          name='email'
          label='Email'
          autoComplete='username'
          variant='outlined'
          color='secondary'
          className={classes.input}
          value={formik.values.email}
          onChange={formik.handleChange}
          error={formik.touched.email && Boolean(formik.errors.email)}
          helperText={formik.touched.email && formik.errors.email}
        />
        <TextField
          fullWidth
          id='password'
          name='password'
          label='Password'
          type='password'
          variant='outlined'
          color='secondary'
          autoComplete='current-password'
          className={classes.input}
          value={formik.values.password}
          onChange={formik.handleChange}
          error={formik.touched.password && Boolean(formik.errors.password)}
          helperText={formik.touched.password && formik.errors.password}
        />
        {/* <Typography className={classes.forgotLink}>
          <Link
            component={NavLink}
            to='/recuperar'
            style={{ cursor: 'pointer' }}
          >
            {' '}
            Esqueceu a senha?
          </Link>
        </Typography> */}
        <Button
          type='submit'
          variant='contained'
          color='primary'
          fullWidth
          style={{ backgroundColor: '#1f497d' }}
          className={classes.submitBtn}
        >
          Entrar
        </Button>
        {/* <Typography className={classes.divider} component='div'>
          <span>ou</span>
        </Typography> */}
        {/* <Grid container justify={'space-between'}>
          <Grid item className={classes.paddingRight} xs={6}>
            <Button
              variant='contained'
              fullWidth
              className={classes.googleButton}
              color='primary'
            >
              <img
                alt='Google Icon'
                className={classes.icon}
                src={GoogleIcon}
              />{' '}
              Google
            </Button>
          </Grid>
          <Grid className={classes.paddingLeft} item xs={6}>
            <FacebookButton
              provider='facebook'
              appId={appId}
              scope='public_profile,email'
              onLoginSuccess={(user) =>
                handleFacebookLogin(user._profile, user._token.accessToken)
              }
              onLoginFailure={(err) => console.log(err)}
            />
            <Button
              variant='contained'
              fullWidth
              className={classes.facebookButton}
              color='primary'
            >
              <img
                alt='FacebookIcon'
                className={classes.icon}
                src={FacebookIcon}
              />{' '}
              Facebook
            </Button>
          </Grid>
        </Grid> */}
      </form>
    </Grid>
  )
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      width: '100%',
    },
    colorPrimary: {
      backgroundColor: '#1f497d',
    },
    barColorPrimary: {
      backgroundColor: '#ffffff',
    },
    icon: {
      color: '#fff',
      marginRight: theme.spacing(1.5),
    },
    form: {
      display: 'flex',
      flexDirection: 'column',
      flexGrow: 1,
    },
    input: {
      marginBottom: theme.spacing(2),
    },
    forgotLink: {
      textAlign: 'right',
    },
    submitBtn: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    divider: {
      display: 'flex',
      textAlign: 'center',
      '&>span': {
        marginBottom: 3.5,
        color: '#aaa',
      },
      '&::after': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginBottom: 3,
        marginLeft: theme.spacing(2),
      },
      '&::before': {
        content: '""',
        alignSelf: 'center',
        flexGrow: 1,
        backgroundColor: '#ccc',
        height: 1,
        marginRight: theme.spacing(2),
        marginBottom: 3,
      },
    },

    paddingLeft: {
      paddingLeft: theme.spacing(1),
    },
    paddingRight: {
      paddingRight: theme.spacing(1),
    },

    googleButton: {
      background: '#DD4B38',
    },
  })
)

export default FormLogin