import {
  createStyles,
  Icon,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
} from '@material-ui/core'
import { Dashboard, People, Person, ExitToApp } from '@material-ui/icons'
import clsx from 'clsx'
import { memo } from 'react'
import { useHistory } from 'react-router'
import { Link } from 'react-router-dom'

interface ListProps {
  pathname: string,
  handleClose: (event: React.KeyboardEvent | React.MouseEvent) => void
}

function NavigationList({ pathname, handleClose }: ListProps) {
  //const { removeToken, atobJSON } = useJWT()
  const role = localStorage.getItem('role')
  const { push } = useHistory()
  const isUserAdmin = role === 'Admin'
  const classes = useStyles()
  const logout = () => {
    localStorage.removeItem('role')
    localStorage.removeItem('name')
    localStorage.removeItem('code')
    push('/entrar')
  }
  return (
    <List>
      <ListItem
        button
        onClick={handleClose}
        component={Link}
        to='/dashboard'
        className={clsx([pathname === '/dashboard' && classes.isActive])}
      >
        <ListItemIcon>
          <Dashboard />
        </ListItemIcon>
        <ListItemText primary='Dashboard' />
      </ListItem>
      {/* <ListItem
        button
        component={Link}
        to='/relatorio'
        className={clsx([pathname === '/relatorio' && classes.isActive])}
      >
        <ListItemIcon>
          <Description />
        </ListItemIcon>
        <ListItemText primary='Relatrios' />
      </ListItem> */}
      {(role === 'Admin' || role === 'Supervisor') && (
        <ListItem
          button
          component={Link}
          onClick={handleClose}
          to='/cadastros'
          className={clsx([pathname === '/cadastros' && classes.isActive])}
        >
          <ListItemIcon>
            <People />
          </ListItemIcon>
          <ListItemText primary='Gerenciar Cadastros' />
        </ListItem>
      )}
      <ListItem button component={Link}
        to='/client' onClick={handleClose}>
        <ListItemIcon>
          <Person />
        </ListItemIcon>
        <ListItemText primary='Editar Usurio' />
      </ListItem>
      <ListItem button onClick={e => logout()}>
        <ListItemIcon>
          <ExitToApp />
        </ListItemIcon>
        <ListItemText primary='Sair' />
      </ListItem>
    </List>
  )
}
const useStyles = makeStyles(() =>
  createStyles({
    isActive: {
      backgroundColor: '#6200ee14',
      color: '#6200EE',
      '&>div.MuiListItemIcon-root > svg': { fill: '#6200EE' },
    },
  })
)

export const MemoizedNavigationList = memo(NavigationList)
